body{
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: url('http://bgfons.com/upload/rainbow_texture679.jpg');
    margin: 0;
}
body:before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,0.85);
}


form.signup{
    display: flex;
    position: relative;
    flex-direction: column;
    width: 300px;
}

form.signup input{
    border: none;
    outline: none;
    border-bottom: 1px solid #3D55BC;
    margin-bottom: 1rem;
    font-size: 15px;
    color: #fff;
    padding-bottom: 0.5rem;
    background: none;
}


form.signup button, .game-info button{
    padding: 0.5rem 0.3rem;
    background: #3D55BC;
    border: none;
    outline: none;
    color: #fff;
    transition: .3s;
    cursor: pointer;
}
form.signup button:hover, .game-info button:hover, .square:hover{
    background: #fff;
    color: #3D55BC  
}

/*tic_tac*/

ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: #fff;
    border: 1px solid #3D55BC;
    float: left;
    color: #3D55BC;
    font-size: 36px;
    font-weight: bold;
    line-height: 34px;
    height: 72px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 72px;

}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: flex;
    flex-direction: row;
    position: relative;
    color: #3D55BC;
}

.game-board{
    position: relative;
    box-shadow: 10px 10px 10px 10px rgba(0,0,0,0.85);
}

.game-info{
    position: absolute;
    width: 200px;
    left: 233px;
    top: -78px
}
.game-info button{
    margin-bottom: 0.5rem;
}
.winner{
    position: absolute;
    width: 90%;
    height: 4px;
    background: #3D55BC;
    margin-top: 2rem;
    left: 9px;


}

.winner1{
    top: 73px;
    left: 12px
}
.winner2{
    top: 145px;
    left: 12px
}
.winner3{
    transform: rotate(90deg);
    left: -60px;
    top: 86px
}

.winner4{
    transform: rotate(90deg);
    left: 10px;
    top: 93px
}

.winner5{
    transform: rotate(90deg);
    left: 82px;
    top: 89px
}


.winner6{
    transform: rotate(45deg);
    top: 73px;
    left: 0px;
    width: 100%;

}

.winner7{
    transform: rotate(-45deg);
    top: 73px;
    left: 0px;
    width: 100%;
}

.game-info {
    margin-left: 20px;
}
